import React from "react"
import Layout from "../components/Layout"
import * as styles from "../styles/about.module.css"

import logo from "../images/Chefs Choice Logo.png";

import about1 from "../images/aboutImages/about1.png";
import about2 from "../images/aboutImages/about2.png";
import about3 from "../images/aboutImages/about3.png";
import about4 from "../images/aboutImages/about4.png";
import about5 from "../images/aboutImages/about5.png";


export default function About() {

  // Window Undefined Handle
  if(typeof window === "undefined") {
    return;
  }

  // Language 
  const [language, setLanguage] = React.useState('');
  React.useEffect(() => {
    setLanguage(window.localStorage.getItem('CC-Language'));
  }, []);


  // Scroll FX
  const innerHeight = window.outerHeight;
  React.useEffect(() => {
    document.getElementById('about').style.height = innerHeight*5 + "px"; // About height set
  }, []);   

  const handleScroll = React.useCallback((e) =>
    {

      if (window.innerWidth > 800) {
        e.preventDefault();

        const section1 = document.getElementById("section1");
        const section2 = document.getElementById("section2");
        const section3 = document.getElementById("section3");
        const section4 = document.getElementById("section4");
        const section5 = document.getElementById("section5");
  
        const scrollY = window.pageYOffset;
  
        if (scrollY < innerHeight) {
          section1.style.top = -scrollY + 'px';
          section2.style.top = '0px';
        }
        if (scrollY >= innerHeight) {
          section1.style.top = -innerHeight + 'px';
          section2.style.top = -(scrollY-innerHeight) + 'px';
          section3.style.top = '0px';
        }
        if (scrollY >= 2*innerHeight) {
          section2.style.top = -innerHeight + 'px';
          section3.style.top = -(scrollY-2*innerHeight) + 'px';
          section4.style.top = '0px';
        }
        if (scrollY >= 3*innerHeight) {
          section3.style.top = -innerHeight + 'px';
          section4.style.top = -(scrollY-3*innerHeight) + 'px';
          section5.style.top = '0px';
        }
        if (scrollY >= 4*innerHeight) {
          section4.style.top = -innerHeight + 'px';
          section5.style.top = -(scrollY-4*innerHeight) + 'px';
        }
        if (scrollY >= 5*innerHeight) {
          section5.style.top = -innerHeight + 'px';
        }

      }
    }, []);

  React.useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    }
  }, []);


  return (
    <Layout>
        <div className={styles.About} id="about">

            <section className={styles.section1} id="section1">
              <img src={logo} alt="logo" className={styles.logo}/>
              <h2 className={styles.headingSection1}>{language === "tr-TR" ? "Chef's Choice olarak biz önce kaliteye önem veririz." : "We, at Chef's Choice, are all about quality."}</h2>
              <p className={styles.textSection1}>
                {language === "tr-TR" ? 
                "Chef's Choice olarak, lezzet ve besin değerlerini ön planda tutarak kedi ve köpekler için mükemmel mamayı yaratmayı hedefleyen şefler, beslenme uzmanları, veterinerler ve evcil hayvan severlerden oluşan bir aileyiz. Evcil hayvanınızın tükettiği mamanın kalitesine ne kadar değer verdiğinizi biliyoruz ve aynı kaygıları paylaşarak, dostlarımız için birinci sınıf mamalar yaratmak hedefiyle tariflerimizi formüle ediyoruz." 
                : 
                "As Chef’s Choice, we are a family of chefs, nutritionists, veterinarians, and pet lovers aiming to create the perfect food for cats and dogs by prioritizing taste and nutritional values. We know how much you value the quality of food that your pet consumes, and by sharing the same concerns, we formulate our recipes with the aim of creating top-notch food for our buddies."}
              </p>
              <img src={about2} alt="hayvan" className={styles.imgSection1} />
            </section>

            <section className={styles.section2} id="section2">
              <h2 className={styles.headingSection2}>{language === "tr-TR" ? "Kendimizi evcil hayvanınıza birinci sınıf yemekler sağlamaya adadık." : "We are dedicated to provide your pet with top-notch meals."}</h2>
              <p className={styles.textSection2}>
                {language === "tr-TR" ? 
                "%100 taze et ile formüle edilen yaş mamalarımız, doğal ve sağlıklı malzemelerle üretilmiştir. Tavuk, kuzu, somon ve sığır eti gibi en kaliteli etleri balkabağı, havuç ve pirinç gibi besleyici malzemelerle bir araya getiriyoruz. Her evcil hayvan benzersizdir, benzersiz ihtiyaçları vardır. Bu nedenle, tariflerimizin her biri farklı beslenme ihtiyaçlarını ve vücutta bunlara karşılık gelen işlevleri hedefler. Seçtiğimiz tariflerle, arkadaşınızın sağlığını, büyümesini ve gelişimini desteklerken farklı tatları keşfetmesine yardımcı olmayı amaçlıyoruz." 
                : 
                "Our paté’s, formulated with 100% fresh meat, are made with natural and wholesome ingredients. We combine the highest quality meats, such as chicken, lamb, salmon, and beef with nutritious ingredients like pumpkins, carrots and rice. Each pet is unique, having unique needs. Thus, each one of our distinctive recipes target different nutritional needs, and their corresponding functions in the body. Through our selection of recipes, we aim to help your buddy explore different tastes while supporting their health, growth, and development."}
              </p>
              <img src={about1} alt="hayvan" className={styles.imgSection2} />
            </section>

            <section className={styles.section3} id="section3">
              <h2 className={styles.headingSection31}>{language === "tr-TR" ? "Dostlarımız lezzeti hak eder." : "Every pet deserves a delicious meal."}</h2>
              <h2 className={styles.headingSection32}>{language === "tr-TR" ? "İlave şeker veya tuza ihtiyac yoktur." : "Nobody needs added sugar or salt."}</h2>
              {language === "tr-TR" ? 
                <p className={styles.textSection3}>
                Çoğu evcil hayvan maması şirketinin, evcil hayvanlarımızın mamalarını daha lezzetli hale getirmek için içlerine şeker ve tuz koyduğunu biliyor muydunuz? Şeker ve tuz kilo alımı, evcil hayvan obezitesi ile bağlantılıdır ve diyabetin önde gelen nedenidir. 
                Ayrıca diş hasarına neden olabilir. Şeker ve tuz büyük olasılıkla kalitesiz malzemelerin tadını bastırmak, yiyeceği daha lezzetli hale getirmek ve yiyeceğin dokusunu iyileştirmek için kullanılır.
                <br/><br/>
                Özenle formüle edilmiş yemeklerimizin lezzetini en kaliteli malzemeler ve taze etlerle elde ediyoruz.
                </p> 
                : 
                <p className={styles.textSection3}>
                Did you know that most pet food companies put sugar and salt in our pets’ food to make them extra tasty? Sugar and salt is linked with weight gain, pet obesity and is a leading cause of diabetes. 
                It also may cause dental damage. Sugar and salt is most likely used to mask the taste of poor quality ingredients, make the food more palatable and to improve the texture of the food.
                <br/><br/>
                We achieve the deliciousness of our carefully formulated foods with the finest quality ingredients and fresh meats.
                </p>
                }
              <img src={about3} alt="hayvan" className={styles.imgSection3} />
            </section>

            <section className={styles.section4} id="section4">
              <h2 className={styles.headingSection41}>{language === "tr-TR" ? "Karşı konulamaz tat." : "Irresistible taste."}</h2>
              <h2 className={styles.headingSection42}>{language === "tr-TR" ? "%100 Taze et, aroma yok." : "100% Fresh meat, no aromas."}</h2>
              <p className={styles.textSection4}>
                {language === "tr-TR" ? 
                "Evcil hayvan mamaları Michelin Yıldızları ile ödüllendirilseydi, biz 3 taneye sahip olurduk. Mutfağımızdan evcil hayvanınızın kasesine kadar, üretimimiz en taze ve en kaliteli malzemeleri içerir, temiz ve profesyoneldir." 
                : 
                "If pet foods were awarded Michelin Stars we would have 3. From our kitchen to your pet’s bowl, our production is neat and professional, containing the most fresh and high quality ingredients."}
              </p>
              <img src={about4} alt="hayvan" className={styles.imgSection4} />
            </section>

            <section className={styles.section5} id="section5">
              <h2 className={styles.headingSection51}>{language === "tr-TR" ? "Lezzetli ve sağlıklı." : "Delicious and healthy."}</h2>
              <h2 className={styles.headingSection52}>{language === "tr-TR" ? "Sonunda bir arada." : "Together at last."}</h2>
              <p className={styles.textSection5}>
                {language === "tr-TR" ? 
                "Gurme mamalarımız mısır, soya, şeker, tuz, GDO, yapay aromalar, koruyucular ve telaffuzu zor ve sindirimi daha da zor olan diğer katkı maddeleri içermez." 
                : 
                "Our finest gourmet dishes are free from corn, soy, sugar, salt, GMO, artificial aromas, preservatives and any other additives that are hard to pronounce and even harder to digest."}
              </p>
              <img src={about5} alt="hayvan" className={styles.imgSection5} />
            </section>

        </div>
    </Layout>
  )
}
