import { Link } from "gatsby";
import React from "react";
import * as styles from "../styles/footer.module.css";
import logo from "../images/Chefs Choice Logo.png";

import instagram from "../images/Icons/Icon1.svg";


export default function Footer(props) {

    const [email, setEmail] = React.useState('');

    // Language 
    const [language, setLanguage] = React.useState('');
    React.useEffect(() => {
      setLanguage(window.localStorage.getItem('CC-Language'));
    }, []);


  return (
    <div className={styles.Footer} id="footer">

        <img src={logo} alt="logo" className={styles.logo}/>
    
        <form className={styles.newsletter}>
            <h3 className={styles.heading}>{language === 'en-EU' ? 'Sign up to our newsletter to stay up-to-date with our products and news!' : 'Ürünlerimizden ve gelişmelerden haberdar olmak için bültenimize abone olun!'}</h3>
            <input className={styles.email} type='text' name='email' placeholder={language === 'en-EU' ? 'Enter your email...' : 'Emailinizi yazin...'} value={email} onInput={(e) => setEmail(e.target.value)} autoComplete="off" />
            <input type="submit" value={language === 'en-EU' ? 'SIGN UP' : 'ABONE OL'} className={styles.signup}/>
        </form> 

        <div className={styles.sitemapContainer}>
          <div className={styles.sitemap}>
            <h2>{language === "tr-TR" ? "SİTE HARİTASI" : "SITEMAP"}</h2>
            <Link to="/" className={styles.sitemapLink}>{language === "tr-TR" ? "ANASAYFA" : "HOMEPAGE"}</Link>
            <Link to="/products" className={styles.sitemapLink}>{language === "tr-TR" ? "ÜRÜNLER" : "PRODUCTS"}</Link>
            <Link to="/about" className={styles.sitemapLink}>{language === "tr-TR" ? "HAKKIMIZDA" : "ABOUT"}</Link>
            <Link to="/contact" className={styles.sitemapLink}>{language === "tr-TR" ? "İLETİŞİM" : "CONTACT US"}</Link>
          </div>
          <div className={styles.siteContacts}>
            <a href="https://www.instagram.com/chefschoicetr/?hl=en" target="_blank" rel="noreferrer"><img src={instagram} alt="instagram logo" className={styles.icon}/></a>
            <Link to="/contact" className={styles.contactButton}>{language === 'tr-TR' ? "İLETİŞİME GEÇ" : "GET IN TOUCH"}</Link>
          </div>
        </div>

        <div className={styles.subFooter}>
            <Link to="/privacypolicy" className={styles.link}><p>{language === "tr-TR" ? "Gizlilik Politikası" : "PRIVACY POLICY"}</p></Link>
            <Link to="/termsofuse" className={styles.link}><p>{language === "tr-TR" ? "Kullanım Koşulları" : "TERMS OF USE"}</p></Link>
            <p className={styles.subFooterText}>ATLASJET HAVACILIK SAN. LTD. ŞTİ. 2022 © {language === "tr-TR" ? "Tüm hakları saklıdır." : "All Rights Reserved."}</p>
        </div>

    </div>
  )
}